@charset "UTF-8";
/*-------------------------------------------
formArea
-----------------------------------------------*/
.formArea .first p {
  line-height: 2;
  margin-bottom: 1em;
}

.formArea .first p a {
  color: #fa0042;
}

.formArea select,
.formArea input,
.formArea textarea {
  padding: 0.5em;
}

.formArea textarea {
  width: 100%;
  box-sizing: border-box;
}

.formArea .btn {
  margin-top: 2em;
  text-align: center;
}

.formArea input[type=submit] {
  -webkit-appearance: none;
  width: 100%;
  color: #fff;
  background: #25b337;
  border: none;
  padding: 2em 0;
  cursor: pointer;
}

.header-inner {
  display: none !important;
}

.entry-content {
  max-width: 760px;
  margin: 0 auto;
}

.entry-content > p,
.entry-content > div {
  max-width: 100% !important;
}

h1 {
  color: #333 !important;
}

h1,
.heading-size-1,
h2 {
  font-size: 24px;
  font-weight: 800;
  line-height: 1.5;
}

@media (min-width: 700px) {
  h1,
.heading-size-1,
h2 {
    font-size: 28px;
  }
}
@media (min-width: 1220px) {
  h1,
.heading-size-1,
h2 {
    font-size: 28px;
  }
}
.entry-content > p,
.entry-content > div {
  max-width: 100% !important;
}

h2 {
  background-color: #FF6893;
  color: #fff !important;
  text-align: center;
  padding: 0.5em 0;
  width: 100vw !important;
  margin: 6rem calc(50% - 50vw) 3rem !important;
  max-width: 100vw !important;
  position: relative;
}
h2::before {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -20px;
  border: 20px solid transparent;
  border-top: 20px solid #ff6893;
}

h2 p {
  line-height: 1.7 !important;
}

p.powered-by-wordpress {
  display: none;
}

#site-footer .section-inner {
  justify-content: center;
  color: #333 !important;
}

.to-the-top {
  display: none;
}

div.formContainer {
  background-color: #fff;
  width: 100vw !important;
  max-width: 100vw !important;
  margin: 6rem calc(50% - 50vw) 0 !important;
  padding-bottom: 6rem;
}

.formArea {
  max-width: 90% !important;
  margin: 0 auto;
}
.formArea .star {
  color: #ee1c1c;
}
.formArea p {
  color: #333 !important;
}
.formArea .input-row {
  display: inline-block;
}

@media screen and (max-width: 321px) {
  .formArea {
    max-width: 96% !important;
  }
}
@media screen and (max-width: 650px) {
  .formArea th,
.formArea td {
    display: block !important;
    border-width: 0 !important;
    padding-bottom: 0 !important;
  }
  .formArea tr {
    display: block;
    margin-bottom: 2em;
  }
  .formArea p {
    margin-bottom: 0 !important;
  }
}
@media screen and (min-width: 768px) {
  .formArea {
    max-width: 760px !important;
  }
}
h2.midashi {
  background-color: #25b337;
  margin-top: 0 !important;
  margin-bottom: 4rem !important;
  font-family: "游明朝", YuMincho, "ヒラギノ明朝 ProN W3", "Hiragino Mincho ProN", "HG明朝E", "ＭＳ Ｐ明朝", "ＭＳ 明朝", serif;
}
h2.midashi::before {
  border-top: 20px solid #25b337;
}

.post-meta-wrapper {
  display: none !important;
}

#site-footer {
  margin-top: 0 !important;
  background-color: #F4F2E8;
}

.admin-info {
  text-align: center;
  padding-top: 3rem;
}

.color-accent,
.color-accent-hover:hover,
.color-accent-hover:focus,
:root .has-accent-color,
.has-drop-cap:not(:focus):first-letter,
.wp-block-button.is-style-outline,
a {
  color: #fa0042;
}

.small-text p {
  font-size: 80%;
}